import _ from 'lodash';
import firebase from 'firebase';
import Immutable from 'immutable';


const INITIAL_STATE = {
    status: 'INITIAL',
    contacts: null,
    contactStatus: null,
    contactEditing: null
};


export function doLogin({email, password}) {
    return {
        types: ['LOGIN', 'LOGIN_SUCCESS', 'LOGIN_FAIL'],
        promise: () => firebase.auth().signInWithEmailAndPassword(email, password)
    };
}

export function setContactStatus(contact, status) {
    return {type: 'SET_CONTACT_STATUS', contact, status};
}

export function editContact(contact) {
    return {type: 'EDIT_CONTACT', contact};
}

export function addContact() {
    return {type: 'ADD_CONTACT'};
}

export function saveContact(contact){
    return (dispatch) => {
        dispatch(setContactState(contact, 'SAVING'));

        const contacts = firebase.database().ref('contacts');
        if (contact.id) {
            delete contact.id;
            contacts.child(contact.id).set(contact);
        } else {
            var contactRef = contacts.push(contact);
            contact.id = contactRef.key;
            contactRef.then(() => setContactState(contact, 'SAVED'));
        }
    };
}

export function loadContacts() {
    return {
        types: ['contacts/LOAD_CONTACTS', 'contacts/LOAD_CONTACTS_SUCCESS', 'contacts/LOAD_CONTACTS_ERROR'],
        promise: () => {
            return firebase.database().ref('contacts').once('value').then((contactsSnaphot) => {
                return Immutable.OrderedMap(
                        _(contactsSnaphot.val())
                         .map((contact, key) => ({
                             ...contact,
                             state: 'INITIAL',
                             key
                         }))
                         .orderBy(['region', 'name'], ['asc', 'asc'])
                         .map(contact => [contact.key, Immutable.Map(_.omit(contact, 'key'))])
                         .valueOf()
                );
            })
        }
    };
}

export default function reducer(state = INITIAL_STATE, action) {
    switch (action.type) {
    case 'contacts/LOAD_CONTACTS_SUCCESS':
        // console.log(_(action.result.toArray()).map(contact => contact.toObject()).groupBy('region').valueOf());
        return {...state, contacts: action.result, status: 'LOADED'};
    case 'SET_CONTACT_STATUS':
        return {...state,
                contacts: state.contacts.set(action.contact.key, action.contact),
                status: action.status};
    case 'EDIT_CONTACT':
        return {...state, contactEditing: action.contact.id, contactStatus: 'INITIAL'};
    case 'ADD_CONTACT':
        return {...state, contacts: [...state.contacts, {id: 'NEW'}], contactEditing: 'NEW', contactStatus: 'INITIAL'};
    default:
        return state;
    }
}



