import _ from 'lodash';
import React from 'react';
import ReactDOM from 'react-dom';
import { Provider, connect } from 'react-redux';
import {doLogin, saveContact, loadContacts} from './redux';
import {store} from './store';


store.dispatch(loadContacts());


class ContactList extends React.Component {
    render() {
        const {contacts} = this.props;

        if (!contacts) {
            return null;
        }

        return (<div className="contacts">
            {_(contacts.toArray()).map(contact => contact.toObject())
                                  .groupBy('region')
                                  .map((regionContacts, region) =>
                <div className="region">
                    <h2>{region}</h2>
                    {regionContacts.map(contact => <div className="contact">
                        <strong>{contact.name}</strong>: {contact.description}
                    </div>)}
                </div>
            ).valueOf()}
        </div>);
    }
}
ContactList = connect(
    state => ({contacts: state.contacts.contacts})
)(ContactList);


ReactDOM.render(<Provider store={store}>
    <ContactList />
</Provider>, document.querySelector('.contact-list'));



